<style scoped>
  /*html,*/
  /*body {*/
  /*padding: 0;*/
  /*margin: 0;*/
  /*height: 100%;*/
  /*width: 100%;*/
  /*background: #000;*/
  /*}*/

  .mi-item {
    padding: 0 0 3px;
    position: absolute;
    background: #fff;
    list-style: none;
    width: 100%;
  }

  .mi-item::after {
    content: "";
    position: absolute;
    bottom: 1.5px;
    left: 150px;
    right: 0;
    border-bottom: 1px solid #e4e4e4;
  }

  .version-item {
    display: flex;
    display: -webkit-box;
    box-align: center;
    -webkit-box-align: center;
    width: 100%;
  }

  .version-item-img {
    width: 180px;
    height: 180px;
    position: relative;
  }

  .version-item-img img {
    width: 100%;
  }

  .version-item .version-item-intro {
    -webkit-box-flex: 1;
    box-flex: 1;
    display: block;
    padding: 0 15px 5px;
  }

  .version-item .version-item-intro .version-item-name {
    font-size: 14px;
    color: rgba(0, 0, 0, .87);
    margin-bottom: 8px;
  }

  .version-item .version-item-intro .version-item-brief {
    font-size: 12px;
    color: rgba(0, 0, 0, .54);
    margin-bottom: 8px;
    line-height: 15px;
    overflow: hidden;
  }

  .version-item-brief p {
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
  }

  .version-item .version-item-intro .version-item-intro-price {
    font-size: 15px;
    position: relative;
    margin-left: 10px;
  }

  .version-item .version-item-intro span {
    color: #ff6000;
  }

  .mi-item.tombstone p {
    width: 100%;
    height: 0.5em;
    background-color: #ccc;
    margin: 0.5em 0;
  }
</style>
<script>
import offerItem from './OfferItem.vue'
import offerTombstone from './OfferTombstone.vue'
import offerFetc from './offer-fetch'
import axios from 'axios'

export default ({
  data () {
    return {
      item: offerItem,
      tombstone: offerTombstone,
      fetch: offerFetc,
      items: [],
      loading: false,
      post: false,
      error: null
    }
  },
  props: {
    search: ''
  },
  methods: {
    async searchOffers () {
      try {
        this.loading = true
        await axios.post(`/offers`, {
            city: this.search,
            offerType: 'Room',
            roomType: 'SinglePerson',
            minPrice: '700',
            maxPrice: '2000'
          }
        ).then(result => {
          console.log(result.data)
          this.items = result.data
          this.loading = false
          this.post = true
        })
      } catch (e) {
        console.log(e.message)
      }
    },
    fetchItems (limit, skip) {
      return {
        list: this.items,
        count: this.items.length
      }
    }
  },
  created () {
    this.searchOffers()
  }
})
</script>
<template>
  <div id="recyclerview">
    <div class="loading" v-if="loading">
    Loading...
  </div>
    <RecyclerView v-if="post" class="content"
      style="height: 80vh"
      :prerender="30"
      :fetch="fetch"
      :item="item"
      :tombstone="tombstone"
    ></RecyclerView>
  </div>
</template>



